

import { Box, Button, Tooltip,Text, Menu, MenuButton, MenuList, Avatar, MenuItem, MenuDivider, Input,  useToast, Spinner, effect } from '@chakra-ui/react'
import React, { useState } from 'react'
import { BellIcon,ChevronDownIcon} from '@chakra-ui/icons'
import { ChatState } from '../../context/ChatProvider'
import ProfileModal from './ProfileModal'
import { useHistory } from 'react-router-dom'
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
} from '@chakra-ui/react'
import {useDisclosure} from '@chakra-ui/hooks'
import ChatLoading from '../ChatLoading'
import UserListItem from '../UserAvatar/UserListItem'
import { getSender } from '../../config/ChatLogics'
import NotificationBadge from "react-notification-badge"
import {Effect} from "react-notification-badge"
const axios=require("axios");
const SideDrawer = () => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const history=useHistory();
    const [search, setSearch] = useState("")
    const [searchResult, setSearchResult] = useState([])
    const [loading, setLoading] = useState(false)
    const [loadingChat, setLoadingChat] = useState(false)
    const toast=useToast()
   

    const {user,selectedChat,setSelectedChat,chats,setChats,notification,setNotification}=ChatState()

    const logoutHandler=()=>{
      localStorage.removeItem("userInfo");
      history.push("/") 
    }

    const handleSearch = async () => {
      if (!search) {
        toast({
          title: "Please Enter something in search",
          status: "warning",
          duration: 5000,
          isClosable: true,
          position: "top-left",
        });
        return;
      }
  
      try {
        setLoading(true);
  
        const config = {
          headers: {
            Authorization: `Bearer ${user.token}`,
          },
        };
  
        const { data } = await axios.get(`/api/user?search=${search}`, config);
  
        setLoading(false);
        setSearchResult(data);
      } catch (error) {
        toast({
          title: "Error Occured!",
          description: "Failed to Load the Search Results",
          status: "error",
          duration: 5000,
          isClosable: true,
          position: "bottom-left",
        });
      }
    };

    const accessChat=async(userId)=>{
      try {
        setLoadingChat(true)
        const config={
          headers:{
            "Content-Type":"application/json",
            Authorization:`Bearer ${user.token}`,
          },
        };

        const {data}=await axios.post('/api/chat',{userId},config);

        if(!chats.find((c)=>c._id===data._id)) setChats([data,...chats])
        setSelectedChat(data);
        setLoadingChat(false);
        onClose()
      } catch (error) {
        toast({
          title: "Error Fetching the chat",
          description: error.message,
          status: "error",
          duration: 5000,
          isClosable: true,
          position: "bottom-left",
        });
      }
    }
  return (
    <>
      <Box
        display="flex"
        justifyContent="space-between"
        alignItems="center"
        bg="white"
        w="100%"
        p="5px 10px 5px 10px"
        borderWidth="5px"
        
        >
        <Tooltip label="Search Users to chat" hasArrow placement='bottom-end'>
        <Button variant="ghost" onClick={onOpen}>
        <i className="fa-solid fa-magnifying-glass"></i>
        <Text display={{base:"none",md:"flex"}} px="4">
            Search User
        </Text>
        </Button>
        </Tooltip>
        <Text fontSize="2xl" fontFamily="Work sans">Gossip</Text>
        <div>
            <Menu>
                <MenuButton p={1}>
                  <NotificationBadge
                  count={notification.length}
                  effect={Effect.SCALE}
                  />
                    <BellIcon fontSize="2xl" m={1}/>
                </MenuButton>
                <MenuList pl={2}>
                {!notification.length && "No New Messages"}
                {notification.map((notify)=>(
                  <MenuItem key={notify._id} onClick={()=>{
                    setSelectedChat(notify.chat)
                    setNotification(notification.filter((n)=>n!==notify));
                  }}>
                    {notify.chat.isGroupChat?`New Message in ${notify.chat.chatName}`:`New Message from ${getSender(user,notify.chat.users)}`}
                  </MenuItem>
                ))}
                </MenuList>
            </Menu>
            <Menu>
              <MenuButton as={Button} rightIcon={<ChevronDownIcon/>}>
                <Avatar size='sm' cursor='pointer' name={user.name} src={user.pic}/>
              </MenuButton>
              <MenuList>
                <ProfileModal user={user}>
                <MenuItem>My Profile</MenuItem>
                </ProfileModal>
                <MenuDivider/>
                <MenuItem onClick={logoutHandler}>Logout</MenuItem>
              </MenuList>
            </Menu>
        </div>
      </Box>

      <Drawer placement='left' onClose={onClose} isOpen={isOpen}>
        <DrawerOverlay/>
        <DrawerContent>
          <DrawerHeader borderBottomWidth="1px">
            Search Users
          </DrawerHeader>
          <DrawerBody>
          <Box display="flex" pb={2}>
              <Input
              placeholder='Search by name or email'
              mr={2}
              value={search}
              onChange={(e)=>setSearch(e.target.value)}
              />
              <Button 
              onClick={handleSearch}
              >Go</Button>
          </Box>
          {loading ? (
              <ChatLoading />
            ) : (
              searchResult?.map((user) => (
                <UserListItem
                  key={user._id}
                  user={user}
                  handleFunction={() => accessChat(user._id)}
                />
              ))
            )}
            {loadingChat && <Spinner ml="auto" display="flex"/>}
        </DrawerBody>
        </DrawerContent>
        
      </Drawer>
    </>
  )
}

export default SideDrawer
